'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { mockAnomaService, MockIntent } from '@/lib/mockAnomaService';

interface PostIntentFormProps {
  onIntentCreated?: (intent: MockIntent) => void;
}

export default function PostIntentForm({ onIntentCreated }: PostIntentFormProps) {
  const [formData, setFormData] = useState({
    intentType: '' as 'teach' | 'learn' | '',
    skillName: '',
    skillLevel: '' as 'beginner' | 'intermediate' | 'advanced' | 'expert' | '',
    availability: '',
    location: '',
    description: ''
  });
  
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    setError(null);
    setSuccess(null);
  };

  const validateForm = () => {
    if (!formData.intentType) {
      setError('Please select whether you want to teach or learn');
      return false;
    }
    if (!formData.skillName.trim()) {
      setError('Please enter a skill name');
      return false;
    }
    if (!formData.skillLevel) {
      setError('Please select your skill level');
      return false;
    }
    return true;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    setIsSubmitting(true);
    setError(null);

    try {
      const intent = await mockAnomaService.postIntent({
        intentType: formData.intentType as 'teach' | 'learn',
        skillName: formData.skillName.trim(),
        skillLevel: formData.skillLevel as 'beginner' | 'intermediate' | 'advanced' | 'expert',
        availability: formData.availability.trim() || undefined,
        location: formData.location.trim() || undefined,
        description: formData.description.trim() || undefined
      });

      setSuccess(`Intent created successfully! Your ${formData.intentType} intent for "${formData.skillName}" is now active.`);
      onIntentCreated?.(intent);
      
      // Reset form
      setFormData({
        intentType: '',
        skillName: '',
        skillLevel: '',
        availability: '',
        location: '',
        description: ''
      });
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to create intent');
    } finally {
      setIsSubmitting(false);
    }
  };

  const currentUser = mockAnomaService.getCurrentUser();
  
  if (!currentUser) {
    return (
      <Card className="mystical-card">
        <CardContent className="pt-6 text-center">
          <div className="text-4xl mb-4"></div>
          <h3 className="text-xl font-bold text-theme-white mb-2">Wallet Not Connected</h3>
          <p className="text-gray-300 mb-4">
            Please connect your Anoma wallet to post an intent.
          </p>
          <Button className="btn-primary">
            Connect Wallet
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="mystical-card">
      <CardHeader>
        <CardTitle className="text-theme-white">Post Your Intent</CardTitle>
        <CardDescription className="text-gray-300">
          Create an intent to teach or learn a skill. Our matching system will find compatible partners for you.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {error && (
            <Alert className="border-theme-red bg-theme-red/10">
              <AlertDescription className="text-theme-red">
                {error}
              </AlertDescription>
            </Alert>
          )}

          {success && (
            <Alert className="border-theme-green bg-theme-green/10">
              <AlertDescription className="text-theme-green">
                {success}
              </AlertDescription>
            </Alert>
          )}

          {/* Intent Type */}
          <div className="space-y-3">
            <Label className="text-theme-white font-medium">What do you want to do?</Label>
            <RadioGroup
              value={formData.intentType}
              onValueChange={(value) => handleInputChange('intentType', value)}
              className="flex gap-6"
            >
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="teach" id="teach" className="border-theme-white" />
                <Label htmlFor="teach" className="text-theme-white cursor-pointer">
                  I want to teach a skill
                </Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="learn" id="learn" className="border-theme-white" />
                <Label htmlFor="learn" className="text-theme-white cursor-pointer">
                  I want to learn a skill
                </Label>
              </div>
            </RadioGroup>
          </div>

          {/* Skill Name */}
          <div className="space-y-2">
            <Label htmlFor="skillName" className="text-theme-white font-medium">
              Skill Name *
            </Label>
            <Input
              id="skillName"
              value={formData.skillName}
              onChange={(e) => handleInputChange('skillName', e.target.value)}
              placeholder="e.g., React Development, UI/UX Design, Python Programming"
              className="mystical-input"
            />
          </div>

          {/* Skill Level */}
          <div className="space-y-2">
            <Label className="text-theme-white font-medium">
              {formData.intentType === 'teach' ? 'Your Skill Level *' : 'Desired Learning Level *'}
            </Label>
            <Select value={formData.skillLevel} onValueChange={(value) => handleInputChange('skillLevel', value)}>
              <SelectTrigger className="mystical-input">
                <SelectValue placeholder="Select skill level" />
              </SelectTrigger>
              <SelectContent className="bg-theme-black border-theme-white/20">
                <SelectItem value="beginner" className="text-theme-white hover:bg-theme-red/20">
                  Beginner
                </SelectItem>
                <SelectItem value="intermediate" className="text-theme-white hover:bg-theme-red/20">
                  Intermediate
                </SelectItem>
                <SelectItem value="advanced" className="text-theme-white hover:bg-theme-red/20">
                  Advanced
                </SelectItem>
                <SelectItem value="expert" className="text-theme-white hover:bg-theme-red/20">
                  Expert
                </SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Availability */}
          <div className="space-y-2">
            <Label htmlFor="availability" className="text-theme-white font-medium">
              Availability
            </Label>
            <Input
              id="availability"
              value={formData.availability}
              onChange={(e) => handleInputChange('availability', e.target.value)}
              placeholder="e.g., Weekends 2-4 PM EST, Weekdays after 6 PM"
              className="mystical-input"
            />
          </div>

          {/* Location */}
          <div className="space-y-2">
            <Label htmlFor="location" className="text-theme-white font-medium">
              Location
            </Label>
            <Input
              id="location"
              value={formData.location}
              onChange={(e) => handleInputChange('location', e.target.value)}
              placeholder="e.g., Remote, New York NY, San Francisco CA"
              className="mystical-input"
            />
          </div>

          {/* Description */}
          <div className="space-y-2">
            <Label htmlFor="description" className="text-theme-white font-medium">
              Description
            </Label>
            <Textarea
              id="description"
              value={formData.description}
              onChange={(e) => handleInputChange('description', e.target.value)}
              placeholder={
                formData.intentType === 'teach' 
                  ? "Describe what you can teach, your experience, and teaching approach..."
                  : "Describe what you want to learn, your goals, and current experience level..."
              }
              className="mystical-input min-h-[100px] resize-none"
            />
          </div>

          {/* Submit Button */}
          <Button
            type="submit"
            disabled={isSubmitting}
            className="btn-primary w-full"
          >
            {isSubmitting ? (
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 border-2 border-theme-white border-t-transparent rounded-full animate-spin"></div>
                Creating Intent...
              </div>
            ) : (
              `Post ${formData.intentType ? formData.intentType.charAt(0).toUpperCase() + formData.intentType.slice(1) : ''} Intent`
            )}
          </Button>

          <div className="text-xs text-gray-400 text-center">
            Your intent will be visible to other users for matching. You can update or cancel it anytime.
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
